import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CpoRfidCrudComponent } from './cpo-rfid-crud/cpo-rfid-crud.component';
import { TABS_DEMO_ROUTES } from './cpo-rfid-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : CpoRfidCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class CpoRfidRoutingModule{}